import React, { useEffect, useState } from "react";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { Doughnut } from "react-chartjs-2";
import "./Insights.scss";

ChartJS.register(ArcElement, Tooltip, Legend);

export default function DoughnutChart({ chartdata, labels }) {
  const [data, setData] = useState({});
  useEffect(() => {
    setData({
      labels,
      datasets: [
        {
          label: "Amount",
          data: chartdata,
          backgroundColor: [
            "rgba(255, 99, 132, 0.2)",
            "rgba(54, 162, 235, 0.2)",
            "rgba(255, 206, 86, 0.2)",
            "rgba(75, 192, 192, 0.2)",
            "rgba(153, 102, 255, 0.2)",
            "rgba(255, 159, 64, 0.2)",
          ],
          borderColor: [
            "rgba(255, 99, 132, 1)",
            "rgba(54, 162, 235, 1)",
            "rgba(255, 206, 86, 1)",
            "rgba(75, 192, 192, 1)",
            "rgba(153, 102, 255, 1)",
            "rgba(255, 159, 64, 1)",
          ],
          borderWidth: 1,
        },
      ],
    });
  }, [chartdata, labels]);
  return (
    <div className="chart">
      {chartdata?.length > 0 && labels?.length > 0 ? (
        <Doughnut data={data} />
      ) : (
        <div className="no-chart-data">No data to show</div>
      )}
    </div>
  );
}
